import React, { useState } from "react";
import axios from "axios";

const AddItem = () => {
  const [itemName, setItemName] = useState("");
  const [itemCode, setItemCode] = useState("");
  const [brand, setBrand] = useState("");
  const [qty, setQty] = useState("");

  const itemNameHandler = (event) => {
    setItemName(event.target.value);
  };
  const itemCodeHandler = (event) => {
    setItemCode(event.target.value);
  };
  const brandHandler = (event) => {
    setBrand(event.target.value);
  };
  const qtyHandler = (event) => {
    setQty(event.target.value);
  };

  const submitHandler = (event) => {
    event.preventDefault();

    const formData = {
      itmName: itemName,
      itmCode: itemCode,
      itmBrand: brand,
      itmQty: qty,
    };
    console.log(formData);
    axios
      .post('http://localhost:3002/addItems', formData)
      .then(() => {
        console.log("data posted");
      })
      .catch((err) => {
        console.log("error on posting" + err);
      });

    setItemCode("");
    setItemName("");
    setBrand("");
    setQty("");
  };

  return (
    <form onSubmit={submitHandler}>
      <div className="form-group">
        <label>Item Name</label>
        <input
          type="text"
          className="form-control"
          value={itemName}
          onChange={itemNameHandler}
          placeholder="Enter Product Name"
        />
      </div>
      <div className="form-group">
        <label>Item Code</label>
        <select
          className="form-control"
          value={itemCode}
          onChange={itemCodeHandler}
        >
          <option value=""></option>
          <option value="itm1">itm1</option>
          <option value="itm2">itm2</option>
          <option value="itm3">itm3</option>
          <option value="itm4">itm4</option>
          <option value="itm5">itm5</option>
        </select>
      </div>
      <div className="form-group">
        <label>Brande</label>
        <select className="form-control" value={brand} onChange={brandHandler}>
          <option value=""></option>
          <option value="Apsara">Apsara</option>
          <option value="Camlin">Camlin</option>
          <option value="Reynolds">Reynolds</option>
          <option value="Classmates">Classmates</option>
          <option value="Faber-Castell">Faber-Castell</option>
        </select>
      </div>
      <div className="form-group">
        <label>Quantity</label>
        <input
          type="number"
          className="form-control"
          rows="3"
          value={qty}
          onChange={qtyHandler}
        ></input>
      </div>
      <button type="submit" className="btn btn-primary my-2">
        Save
      </button>
      <button type="button" className="btn btn-primary my-2 mx-2">
        cancel
      </button>
    </form>
  );
};
export default AddItem;



